//axios
import Vue from 'vue'
import axios from 'axios';

let vueobj = new Vue();

/**
 * 
 * @param {Number} status 
 */
const errorHandle = (status, other) => {
  // 
  switch (status) {
    case 400:
      vueobj.$message.error({showClose: true, message: "", duration: 3000});
      break;
    // 401: 
    case 401:
      vueobj.$message.error({showClose: true, message: "session", duration: 3000});
      break;
    // 403 token
    // token
    case 403:
      vueobj.$message.error({showClose: true, message: "", duration: 3000});
      break;
    // 404
    case 404:
      vueobj.$message.error({showClose: true, message: "", duration: 3000});
      break;
    case 500:
      vueobj.$message.error({showClose: true, message: "", duration: 3000});
      break;
    case 504:
      vueobj.$message.error({showClose: true, message: "", duration: 3000});
      break;
    default:
      console.log(other);
  }}


// axios
var instance = axios.create({
  baseURL: "http://"+location.host+":8082",
  // baseURL:"http://127.0.0.1:8082",
  // timeout: 1000 * 12
});
//
instance.interceptors.request.use(function (config) {
  // Do something before request is sent
  // console.log(config);
  config.headers.api_token = localStorage.getItem('api_token');
  // config.headers.api_token = "projAdmin_b935280d0262efa69c83f63c3f7e433ccc34700e";
  return config;
}, function (error) {
  // Do something with request error
  return Promise.reject(error);
});
// 
instance.interceptors.response.use(
  response => {
    // 200
    // 
    if (response.status === 200) {
      if(typeof response.data != 'undefined'){
        return Promise.resolve(response);
      }else{
        return Promise.reject(response);
      }
    } else {
      return Promise.reject(response);
    }
  },
  // 2
  // 
  // 
  // 
  error => {

    const { response } = error;
    if (response) {
      // 2xx
      errorHandle(response.status, response.data.message);
      return Promise.reject(response);

    } else {
      // 
      // eg:statenetwork
      // networkapp.vue
      // 
      //store.commit('changeNetwork', false);
      //toSignin();
      //vueobj.$message.error({showClose: true, message: "", duration: 3000});

      var originalRequest = error.config;
      if(error.code == 'ECONNABORTED' && error.message.indexOf('timeout')!=-1 && !originalRequest._retry){
        // originalRequest._retry = true
        // return axios.request(originalRequest);
        vueobj.$message.error({showClose: true, message: "", duration: 3000});
        return Promise.reject(response);
      }else{

      }
    }
  }
);

export default instance;




